export default class App extends Component {
  constructor (props) {
    super(props)
    this.state = {
      common:{
        header:{
          icon_mini_url:'',
          logo:'',
          chao_mung:'',
          chao_mung_url:'',
          // background:'http://quatet.langsonweb.com/wp-content/uploads/2020/12/bg-header.jpg',
          background:'',
          menu:[]
        },
        lien_he:{
          sdt_zalo:'',
          sdt_hotline:'',
          url_fb:'',
          dia_chi:'',
        },
        footer:{
          about:[],
          privacy:[],
          job:[],
          design_by:'',
          bo_cong_thuong:{
            name:'',
            url:'#'
          }
        },
        code_gg:{
          code_header:'',
          code_body:''
        }
      },
      data:{
        contents: {
            title: "",
            price_del: "",
            price_ins: "",
            short_des: "",
            long_des: "",
            note: ""
        },
        phu: {
            thumnail: "",
            slide_sort: [],
            show_slide: false,
            rating: "5",
            category_name: "",
            category_link: ""
        },
        related_posts: [],
        cart_sp: []
      }
    }
  }
  componentDidMount(){
    let data_sv=window.data.data;
    let cart_sp=this.fs_convert_slide_sort(data_sv);
    this.setState({
      data:{
        contents:data_sv.contents,
        related_posts:data_sv.related_posts,
        cart_sp:cart_sp,
        phu:data_sv.phu,
      }
    })
  }
  fs_convert_slide_sort=(data)=>{
    let cart_sp=data.cart_sp;
    let slide_sort=data.phu.slide_sort;
    //
    let dataSort=[];
    let text_lock='';
    for(let z=0;z<slide_sort.length;z++){
      for(let x=0;x<cart_sp.length;x++){
        if(slide_sort[z]===cart_sp[x].id){
          dataSort.push(cart_sp[x]);
          text_lock+=','+slide_sort[z];
        }
      }
    }
    for(let x=0;x<cart_sp.length;x++){
      if(text_lock.search(','+cart_sp[x].id)===-1){
        dataSort.push(cart_sp[x]);
      }
    }
    return dataSort;
     

}
  render() {
    let {data}=this.state;
    let {common}=window.data;
    return (
      <React.Fragment>
           <Header common={common}/>
           <Category_body
              data={data} 
              common={common}
            />
           <Footer common={common}/>
      </React.Fragment>
    );
  }
}

